___CSS_LOADER_EXPORT___.push([module.id, `.projectDetails{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  /*  background:red;*/
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;

    padding: 50px;
    width: 70%;
}

.projectImages{
    position:relative;
    flex: 1 1 auto;
  /*  background:blue;*/
    max-width: 50%;
}

.projectImages img{
    display: block;
    max-width: 90%;
    max-height: 90%;
    min-height: 5%;

    margin: auto;
}

.imageSelection{
 /*   background: violet; */
    position: absolute;
    width: 100%;
    bottom:3%;
    height: 10px;


}
.imageSelection .box{
    margin: auto;
    display:flex;
    width:10%;
}
.box img{
    flex: 0 1 auto;
/*    background: red;*/

}

.projectDescription{
    flex: 1 1 auto;
    max-width:50%;
    max-height:50%;
 /*   background:yellow;*/
    text-align: initial;
    font-size: 150%;
    
}
.projectDescription .text{
    font-family: 'Roboto', sans-serif;

}
@media (max-width: 600px) {
    .projectDescription{
        flex: 1 1 auto;
        max-width:100%;
        max-height:50%;
        min-width:100%;
        text-align: center;
        font-size: 80%;
        
    }
    .projectDescription>h1{
        flex: 1 1 auto;
        max-width:100%;
        max-height:100%;
        text-align: center;
        font-size: 150%;
        
    }
    .projectDetails{
        min-width: 90%;
        max-width: 300px;
        padding: 0px;
        margin-top: 0px;
        
    }
    
    
  }
