___CSS_LOADER_EXPORT___.push([module.id, `.columns{
  display:flex;
  flex-direction: column;
  padding-right: 10%;
  padding-left: 10%;
}
.rows{
  display: flex;
  flex-direction:row;
  padding:5%;
  flex-wrap: wrap;
  justify-content: center;
}
.rows>div{
    padding:1%;
}
h3{
  justify-content: center;
  display: flex;
}
.menu>a{
    transition: all .2s;
    opacity: 0.6;
    text-decoration: none; /* no underline */
    font-size: 18px;
    font-weight: 600;
    color:rgb(90, 90, 90);
    margin: 0px;
    flex: 1 1 auto;
    flex-direction: column;
    display: flex;
    align-items:center;
}
 .menu {
  display: flex;
  align-items: center;
  justify-items: center;
  max-height: 30px;
  padding-left: 10%;
  padding-right: 10%;
  padding-bottom:10px;
  padding-top:2.5%;
 }

.menu>a:hover{
    opacity: 1.0;
    font-size: 20px;
    border-color:black;
    border-width: 0px 2px 2px 2px;
    border-style:solid;
    animation: jumps 1s ease-in infinite;

}
.menu>a:hover:before{
      content:"▼";
  }
@keyframes jumps {
  100% {
    transform: translateY(10%);
  }
}
