___CSS_LOADER_EXPORT___.push([module.id, `.menu>a{
    transition: all .2s;
    opacity: 0.6;
    text-decoration: none; /* no underline */
    font-size: 18px;
    font-weight: 600;
    color:rgb(90, 90, 90);
    margin: 0px;
    flex: 1 1 auto;
    flex-direction: column;
    display: flex;
}
 .menu {
  display: flex;
  align-items: center;
  justify-items: center;
  max-height: 30px;
  padding-bottom:10px;
  padding-top:2.5%;
 }

.menu>a:hover{
    opacity: 1.0;
    font-size: 20px;
    border-color:black;
    border-width: 2px 2px 0px 2px;
    border-style:solid;
    animation: jumps 1s ease-in infinite;

}
.menu>a:hover:before{
      content:"▼";
  }
@keyframes jumps {
  100% {
    transform: translateY(10%);
  }
}
.perfildiv{
    padding: 5px;
    color:rgb(90, 90, 90);
  /*  background: cyan;*/
}
.perfildiv>img, .perfildiv>p{
  display: block;
  margin: auto;
}
.perfildiv a>img{
  width:5%;
  height: 5%;
}
.perfildiv>p{
  text-align: center;
  font-size: 30px;
 /* background: blue;*/
}

.separator{
  width:80%;
  border-style:solid;
  border-color:rgb(58, 58, 58);
  border-bottom-width: 2px;
  border-top-width: 0;
  border-left-width: 0;
  border-right: 0;
  margin:auto;
  padding-top:10px;
}
@media (max-width: 600px) {
  .menu {
    display: flex;
    align-items: center;
    justify-items: center;
    max-height: 30px;
    margin:auto;
    font-size: 100%;
 }


 .perfildiv a>img{
    padding-top:20px;
    width:30%;
    height: 30%;
  }
  .perfildiv>p{
    text-align: center;
    font-size: 20px;
  }
}
@media (max-width: 284px) {
  .menu>a{
    margin: auto;
 }
}
