___CSS_LOADER_EXPORT___.push([module.id, `/*-----Projects----*/
.flexBox{
    padding-left: 6%;
    padding-right: 6%;

}
.column{
    position: relative;
    float:left;
    width: 20%;
    padding: 2.5%;
}
.column>div>img{
    display: block;
    margin:auto;
    max-width: 90%;
    max-height: 200px;
    min-height: 230px;
    min-width: 5%;
    border-radius: 3%;
}

.column>div>p{
    border-radius:  40px;
    border-top:  inset;
    border-right:  inset;
    position: absolute;
    bottom:20%;
    width: 80%;
    background-color: rgba(128, 128, 128, 0.726);
    text-align: center;
    color:white;
    padding-top:10px;
    padding-bottom: 10px;
    border-radius: 7px;
    font-weight: 600;
    opacity: 5px;
}
/*----------zoom on hover------------*/
.column>div{
    transition: transform .5s;
  }


  .column>div:hover{
    transform: scale(1.1);
  }


@media (max-width: 600px) {
  .separator{
    width:90%;
    padding-top:0px;
  }
  .flexBox{
    display:flex;
    flex-wrap: wrap;
    align-items: center;


  }
  .column{
      min-width: 100%;
      align-items: center;
      padding: 0;
      padding-top: 5px;
  }
  .column>div>img{
      max-width: 90%;
      max-height: 90%;
      min-height: 5%;
      min-width: 5%;
  }

  .column>div>p{
      max-width: 280px;
      min-width: 100%;
      text-align: center;
      color:white;
      padding-top:10px;
      padding-bottom: 6px;
      font-weight: 600;
  }
  }

  @media (width: 1920px) {
  .column>div>img{
    display: block;
    margin:auto;
    max-width: 90%;
    max-height: 200px;
    min-height: 230px;
    min-width: 90%;

/*
    max-height: 90%;
    min-height: 5%;

    */
  }

}
@media(max-width: 600px){
  .column{
    padding-top:6%;
    padding-bottom: 6%;
  }
}
